.services {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(3, 264px);
  justify-content: space-evenly;

  @media (max-width: $adapt-break) {
    margin: 0 auto;

    width: 264px;

    display: flex;
    flex-direction: column;
  }

  &__box {
    text-align: center;

    transition: transform $hover-duration;

    &:hover {
      .services__img {
        width: 100%;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }

      .services__text {
        margin-bottom: 40px;
      }

      box-shadow: 0 9px 18px rgba(37, 41, 49, 0.03);
      border-radius: $b-radius-m;
      
      transform: scale(1.22);
    }

    @media (max-width: $adapt-break) {
      margin-bottom: 60px;
    }
  }
/* stylelint-disable */
  &__img {
    height: 166px;
    width: 184px;

    object-fit: cover;
    object-position: center;
    border-radius: $b-radius-m;

    transition: width $hover-duration;
  }
/* stylelint-enable */
  &__title {
    margin: 40px 0 16px;

    @extend %h1;
    font-size: 20px;
    line-height: 140%;
  }
/* stylelint-disable */
  &__text {
    margin: 0;

    @extend %p;
    font-size: 14px;
    line-height: 150%;

    transition: margin-bottom $hover-duration;
  }
}
/* stylelint-enable */
